import React, { PureComponent, memo } from "react";
import {
  POPUP_IMAGE_RENEW_REQUEST, 
} from "../../../actions/themeActions";
import Iframe from "react-iframe";
import ReactPlayer from "react-player";
import { isOldBrowser } from "../../../utils";
 
const newBrower = !isOldBrowser();
class Media extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      imgUrl: props.itemData.postFileNew,
      videoLoaded: false,
    };

    this.myRef = React.createRef();

  }

  onLoad = (event) => {  if (event.target.getAttribute("data-load") == 1)  this.setState({ imgUrl: event.target.src }); }

   handleDuration = (duration) => {
     const { getVideoDuration, wall } = this.props; 
     if([74,76,77].includes(wall.ThemeRule.themeId)  && wall.Personalization.playFullVideo === 1 || duration<wall.Personalization.slideDelay) getVideoDuration(duration)
  };

  componentDidMount(){

    const { itemData, getVideoDuration, wall } = this.props;
     const myVideoPlayer = document.getElementById(`${itemData.id}-media-player`);

      if(myVideoPlayer){
         if([74,76,77].includes(wall.ThemeRule.themeId)){
            myVideoPlayer.addEventListener('loadedmetadata', () => {
                 getVideoDuration(myVideoPlayer.duration<wall.Personalization.slideDelay || wall.Personalization.playFullVideo === 1 ? myVideoPlayer.duration : wall.Personalization.slideDelay)
              });
           }
        }   

   if (wall.isDisplay && myVideoPlayer && !newBrower) {
      myVideoPlayer.controls = false;
      myVideoPlayer.autoplay = true;
      myVideoPlayer.setAttribute('webkit-playsinline', 'webkit-playsinline');
      myVideoPlayer.setAttribute('playsinline', 'playsinline');
      myVideoPlayer.setAttribute('muted', '');
      myVideoPlayer.preload = "auto"
    }
  }
 

  render() {

        const { itemData, wall, autoplayVideo = true } = this.props;
        const { id } = wall.Wall;
        const { imgUrl, videoLoaded } = this.state;
        const { type, network, imageList, link, filterId, mediaUrl, stories, postFileNew, networkId, mediaClip} = itemData
        const itemId = itemData.id
        const isDisplay = wall.isDisplay;
        const mediaType = type === 3 || type === 5 ? true : false;
        const mediaImage = type === 2 || type === 4 ? true : false;
        const mediaClipActive = (itemData.mediaClip && itemData.mediaClip !='undefined' && String(itemData.mediaClip).length > 0) ? true : false;
        const isWidgetAutoPlay = (!isDisplay && mediaType && wall.ThemeRule.autoPlay && mediaClipActive) ? true : false;
        const isYoutubePost = network.id === 7 ? true : false;
        const multiImages = imageList && imageList.length > 0 ? true : false;

        const multiImagesClass = multiImages && mediaType ? "tb_fc_multi_images_ico_bottom" : "tb_fc_multi_images_ico";
        itemData['mediaUrl'] = (isDisplay || isWidgetAutoPlay) && mediaClipActive ? itemData.mediaClip : itemData.mediaUrl;
        const autoplay = isDisplay ? !newBrower ? true : autoplayVideo : isWidgetAutoPlay;
        const showControl = isDisplay ? false : (wall.User && Object.keys(wall.User).length > 0) ? wall.User.id == 143031 : false;

    return (
      <div
        key={`${itemData.mediaUrl}--${itemId}`}
        className={`tb_fc_media_wrap mediaHolder${itemId}`}
        role="img"
        aria-label="Post Image"
        style={{
          height: "100%",
          background: '#000000'
        }}
      >
        {multiImages && !isDisplay ? (
          <div className={`${multiImagesClass} tb__icon tb-multiple`}> </div>
        ) : null}

        {mediaType && !isDisplay ? (
          isYoutubePost ? (
            <div className="tb_fc_youtube_ico tb__icon tb-youtube"> </div>
          ) : (
            <div className="tb_fc_video_ico tb__icon tb-reels"> </div>
          )
        ) : null}
        
        {mediaImage ? ( 
          <img
            loading="lazy"
            src={imgUrl}
            data-link={link}
            data-load="0"
            data-network={network.id}
            data-wall-id={id}
            data-item-id={itemId}
            data-filter-id={filterId}
            onLoad={this.onLoad}
            draggable={false}
            onError={(e) => POPUP_IMAGE_RENEW_REQUEST(e)}
            style={{ height: `100%` }}
            width={"300px"}
            height={"300px"}
          />
        ) : 

        link && networkId && networkId == 30 && link.indexOf("vk") >= 0 ? (
          <>
            <Iframe
              className="tb-detail-image-iframe"
              frameBorder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              src={`${mediaUrl}&autoplay=1&mute=1`}
              allowFullScreen="true"
              style={{ width: "100%!important" }}
              data-filter-id={filterId}
              data-stories={stories}
              data-type="video"
              data-network={networkId}
              data-link={link}
              data-item-id={itemId}
              data-load={0}
              draggable={false}
              height="1000"
              width="1000"
            ></Iframe>
          </>
        ) : (
            <>
              <img
                  loading="lazy"
                  src={postFileNew}
                  data-load="0"
                  data-network={network.id}
                  data-wall-id={id}
                  data-item-id={itemId}
                  data-filter-id={filterId}
                  onLoad={this.onLoad}
                  draggable={false}
                  onError={(e) => {
                    POPUP_IMAGE_RENEW_REQUEST(e);
                  }}
                style={{
                  width: `100%`,
                  height: `100%`,
                }}
                width={"300px"}
                height={"300px"}
              />   

                <div
                  className="card_video__01"
                  style={{
                    display: !videoLoaded ? "block" : "none",
                    opacity: !videoLoaded ? 1 : 0,
                    width: `100%`,
                    height: `100%`,
                  }} >

                { networkId != 3 ? 
                    <video
                      key={`${this.props.keyRender}-media-player`}
                      poster={postFileNew}
                      id={`${itemData.id}-media-player`}
                      ref={this.myRef}
                      src={autoplayVideo ? `${itemData.mediaUrl}` : null}
                      autoPlay={autoplay}
                      webkit-playsinline={autoplay}
                      playsinline={autoplay}
                      muted={true}
                      loop={1}
                      onError={(e) => { this.setState({ videoLoaded: true }) }}
                      data-type="video"
                      data-network={networkId}
                      data-filter-id={filterId}
                      data-link={link}
                      data-load={0}
                      data-tb={wall.UserDetail.db_table}
                      data-wall-id={wall.Wall.id} data-item-id={itemId}
                      data-owner-id={wall.Wall.owner}
                      height={'400'}
                      width={'400'}
                      controls={showControl}
                  ></video> : 
                
                  <ReactPlayer
                    key={`${this.props.keyRender}-media-player`}
                    poster={postFileNew}
                    onDuration={this.handleDuration}
                    style={{
                      display: "contents",
                      width: `${0.3 * 16}vw`,
                      height: `${0.3 * 9}vw`,
                    }}
                    className="tb_post_vid_005"
                    url={`${mediaClip && mediaClip!='undefined' && String(mediaClip).length > 0 ? mediaClip : mediaUrl}`}
                    playing={isDisplay ? true : false}
                    loop
                    playsinline 
                    config={{
                      file: {
                        attributes: {
                          playsInline: true,
                        },
                      },
                    }} 
                    muted
                    controls={showControl}
                    onReady={(event) => isDisplay ? this.setState({ videoLoaded: false }) : null }
                  /> 
                
                }
                
              </div>
            </>
        )}
      </div>
    );
  }
}


export default memo(Media);