import React, { PureComponent } from "react";
import Author from "./Author/Author";
import Time from "./Time/Time";
import Network from "./Network/Network";
export default class AuthorDetails extends PureComponent {
  render() {

    const { ThemeRule, postData } = this.props;

    const postTime = postData?.createdAt ?? '';
    const author = postData?.author ?? '';
    const network = postData?.network ?? '';
    
    const authorNameStyle = {
      color: ThemeRule.authorColor,
      fontFamily : ThemeRule.authorFont,
      fontStyle : ThemeRule.authorFontVariant
    };

    const sepratorStyle = {
      backgroundColor: ThemeRule.authorColor
    };

    const postAuthor = (postData && ThemeRule.postAuthor && !author.isInstaUser) ? true : false;
    const authorSeprator = (ThemeRule.postAuthor && ThemeRule.postTime) ? true : false;
    

    return ( <>
 
       <div className="tb_mwf_author_wrapper">
       
       { postAuthor || ThemeRule.postTime ?

        <div className={`tb_mwf_author ${ThemeRule.themeId==64 &&  !postData.file ? `no-image` : null}`}>

          { postAuthor ? <Author network={network} author={author} authorClass={'tb_mwf_author_profile'} key={author.username} /> : ''}

          <div className="tb_mwf_author_info">
            { postAuthor ? <div className={`tb_mwf_authorname tb-cTBfont-${ThemeRule.authorFontVariant ? ThemeRule.authorFontVariant : null}`} style={authorNameStyle}><strong>{author.name}</strong></div> : ''}
            <div className="tb_mwf_post_info">
              {postAuthor &&  ![74,76,77].includes(ThemeRule.themeId) ? <div className={`tb_mwf_username tb-cTBfont-${ThemeRule.authorFontVariant}`} style={authorNameStyle}>{author.username}</div> : ''}
              {authorSeprator && postAuthor ? <div className="tb_mwf_seprator" style={sepratorStyle}> </div> : ''}
              {ThemeRule.postTime ? <Time postTime={postTime} timeClass={'tb_mwf_time'} authorNameStyle={authorNameStyle} /> : ''}
            </div>
          </div>
        </div> : null }

        { network.id && ThemeRule.socialAction ? <div className="tb_mwf_social_" style={{ borderBottom : ThemeRule.themeId==78 ? `60px solid ${ThemeRule.iconType==1 ? ThemeRule.iconColor : network.color}` : 'inherit' }}>
          <Network networkClass={'tb_mwf_social_ico'} network={network} ThemeRule={ThemeRule} />
        </div> : null}
      </div> </>
    );
  }
}