  import React, { PureComponent } from "react";
  import ReactHtmlParser from "react-html-parser";
  import { STRING_TO_URL_CONVERT, autoSizeText } from "../../../../utils";
  import ContentWithOutSlack from "./PostContentConvertion";

  export default class PostContentDisplay extends PureComponent {
    constructor(props) {
      super(props);
      this.state = {
        loadContent : [74, 76, 77].includes(props.ThemeRule.themeId) ? false : true
      };
    }
 

    updateText = () => {

      const { ThemeRule } = this.props;
    
      if ([74, 76, 77].includes(ThemeRule.themeId)) { 
         autoSizeText(ThemeRule.trimcontent);
          setTimeout(() => this.setState({ loadContent : true }) , 100);
       }
    };

    render() {
      const { ThemeRule, personalization, item, contentTitle, largeBlock, wall } = this.props;
      const {  loadContent } = this.state;
      const font = ThemeRule.font_varient ? ThemeRule.font_varient.split('-') : {};
      const fontStyle = font.length > 1 ? font[1] : "";
      const fontWeight = ThemeRule.captionFontWeight==3 ? 'bold'  : ThemeRule.captionFontWeight==2 ? '600' : ThemeRule.captionFontWeight==1  ? '100' :
        font.length > 0 ? (font[0].includes("regular") ? "normal" : font[0]) : "";
      const fontFamily = ThemeRule.css_font ? ThemeRule.css_font : "";
      const fontSizes = `${ ThemeRule.fontSize && ![74, 76, 77, 68].includes(ThemeRule.themeId) ? `${ThemeRule.fontSize}px` : 'inherit' }`;
   
      const fontStyles = {
        color: ThemeRule.fontColor,
        fontWeight : fontWeight,
        fontFamily : fontFamily,
        fontStyle : fontStyle,
      };

      const updateStyle = {
        font: ThemeRule.themeId!=68 ? `${fontStyle}` : '',
        WebkitLineClamp:   ThemeRule.trimcontent ? largeBlock ? 10 : ThemeRule.themeId==78 ? 6 : 3 :  "",
        display:   ThemeRule.trimcontent ? '-webkit-box' : 'block',
        fontSize : fontSizes
      };
     
      const contentData = wall.isDisplay ? item.content : item.network.id == 20 ? item.content : STRING_TO_URL_CONVERT(item.content);
      return (
        <div
          id={`tb-ss-post--card${item.id}`}
          className={`post-card-outer tb-cTBfont- ${ThemeRule.font_varient}`}
          aria-label="Post Content"
          style={fontStyles}> 
      
            <div className="resize" style={updateStyle}  onLoad={this.updateText()}>

              { loadContent ? <>
                { contentTitle ? (
                  <div className="tb_bold_txt__">{ReactHtmlParser(contentTitle)}</div>
                ) : null}
                <ContentWithOutSlack
                  key={item.id}
                  data={item}
                  content={contentData}
                  Personalization={personalization}
                  ThemeRule={ThemeRule}
                />   </> : null }
            </div>  
        </div>
      );
    }
  }
